<template>
<div :class="[type === 'textarea' ? 'bh-textarea' : 'bh-input',
	{
		'is-disabled': disabled,
	}]">
	<template v-if="type !== 'textarea'">
		<input 
			v-bind='$attrs' 
			:type="type"
			ref='input' 
			:disabled="disabled"
			:readonly="readonly"
			class="bh-input__inner"
			@input="handleInput"
			@focus="handleFocus"
			@blur="handleBlur"
			@change="handleChange" />
	</template>
	<div v-else class="bh-textarea">
		<textarea 
			v-bind='$attrs' 
			ref='textarea'
			:disabled="disabled"
			:readonly="readonly"
			class="bh-textarea__inner" 
			@input="handleInput"
			@focus="handleFocus"
			@blur="handleBlur"
			@change="handleChange"></textarea>
	</div>
</div>
</template>

<script>
export default {
	name: 'bh-input',
	data() {
		return {
			hovering: false,
			focused: false,
		};
	},
	props: {
		type: {
			type: String,
			default: "text",
		},
		placeholder: {
			type: String,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
		readonly: {
			type: Boolean,
			default: false,
		},
		clearable: {
			type: Boolean,
			default: false,
		},
		modelValue: {
			type: String,
		},
	},
	methods: {
		handleInput(event) {
			// 调用input,将子级的值传递给父级的v-model
			this.$emit('input', event.target.value);
		},
		handleFocus(event) {
			this.focused = true;
			this.$emit('focus', event);
		},
		handleBlur(event) {
			this.focused = false;
			this.$emit('blur', event);
		},
		handleChange(event) {
			this.$emit('change', event.target.value);
		},
	},
}
</script>
