<template>
<div class="bh-rate">
	<span class="bh-rate__item" v-for="(item, key) in max" :key="key" @click="selectValue(item)">
		<bh-icon class='bh-rate__icon' name='star-on' v-if="showDecimalIcon(item)"></bh-icon>
		<bh-icon class='bh-rate__icon' name='star-off' v-else></bh-icon>
	</span>
</div>
</template>

<script>
export default {
	name: 'bh-rate',
	props: {
		value: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 5
		},
		texts: {
			type: Array,
			default() {
				return ['极差', '失望', '一般', '满意', '惊喜'];
			}
		},
	},
	data () {
		return {
			currentValue: this.value,
		};
	},
	computed: {
		checked() {
			return this.value === this.activeValue;
		},
    },
	methods: {
		selectValue(item) {
			this.currentValue = item;
			this.$emit('input', item);
			this.$emit('change', item);
		},
		showDecimalIcon(item) {
			if(this.currentValue < item) {
				return true;
			}
			return false;
		},

	},
}
</script>
