<template>
<div class="bh-input-number">
	<span class="bh-input-number__decrease" @click='decrease'>
		<bh-icon name='minus'></bh-icon>
	</span>
	<span class="bh-input-number__increase" @click='increase'>
		<bh-icon name='plus'></bh-icon>
	</span>
	<bh-input :value='displayValue' @change="handleInputChange"></bh-input>
</div>
</template>

<script>
export default {
	name: 'bh-input-number',
	props: {
		name: String,
		value: {},
		step: {
			type: Number,
			default: 1,
		},
	},
	data() {
		return {
			currentValue: 0,
			userInput: null,
		};
    },
    watch: {
		value: {
			immediate: true,
			handler(value) {
				this.currentValue = value;
				this.userInput = null;
			}
		}
    },
	computed: {
		displayValue() {
			if (this.userInput !== null) {
				return this.userInput;
			}
			return this.currentValue;
		},
	},
	methods: {
		handleInputChange (val) {
			this.$emit('input', val);
		},
		_increase (value, step) {
			return value + step;
		},
		increase () {
			if (this.inputNumberDisabled || this.maxDisabled) return;
			const value = this.value || 0;
			const newVal = this._increase(value, this.step);
			this.setCurrentValue(newVal);
		},
		_decrease (value, step) {
			return value - step;
		},
		decrease () {
			if (this.inputNumberDisabled || this.minDisabled) return;
			const value = this.value || 0;
			const newVal = this._decrease(value, this.step);
			this.setCurrentValue(newVal);
		},
		setCurrentValue (newVal) {
			this.userInput = null;
			this.$emit('input', newVal);
			this.$emit('change', newVal);
			this.currentValue = newVal;
		},
	},
}
</script>
