<template>
<span class="bh-switch" 
    :class="{ 'is-disabled': switchDisabled, 'is-checked': checked }"
    @click="switchValue">
	<input type="checkbox" name="" ref='input' class="bh-switch__input">
	<span class="bh-switch__core"  :style="{ 'background-color': selectColor, 'background-color': selectColor }"></span>
</span>
</template>

<script>
export default {
	name: 'bh-switch',
	props: {
		value: {
			type: [Boolean, String, Number],
			default: false
		},
		activeValue: {
			type: [Boolean, String, Number],
			default: true
		},
		inactiveValue: {
			type: [Boolean, String, Number],
			default: false
		},
		activeColor: {
			type: String,
			default: ''
		},
		inactiveColor: {
			type: String,
			default: ''
		},
		disabled: {
			type: Boolean,
			default: false
		},
	},
	computed: {
		checked() {
			return this.value === this.activeValue;
		},
		switchDisabled() {
			return this.disabled;
		},
		selectColor() {
			if(this.checked) {
				return this.activeColor;
			}
			return this.inactiveColor;
		},
    },
	methods: {
		handleClick(event) {
			console.log('handleClick', event);
			this.$emit('handleClick');
		},
		handleClose(event) {
			event.stopPropagation();
			console.log('handleClose', event);
			this.$emit('handleClose');
		},
		switchValue() {
			!this.switchDisabled && this.handleChange();
		},
		handleChange() {
			const val = this.checked ? this.inactiveValue : this.activeValue;
			console.log(val);
			this.$emit('input', val);
			this.$emit('change', val);
			this.$nextTick(() => {
				// set input's checked property
				// in case parent refuses to change component's value
				this.$refs.input.checked = this.checked;
			});
		},
	},
}
</script>
