<template>
<button class="bh-button" :class="[type ? 'bh-button-' + type : '',]" @click='handleClick'>
	<i :class="icon" v-if="icon"></i>
	<span>
		<slot />
	</span>
</button>
</template>

<script>
export default {
	name: 'bh-button',
	props: {
		type: String,
		icon: {
			type: String,
			default: ''
		},
	},
	methods: {
		handleClick(evt) {
			this.$emit('click', evt);
		}
    },
	mounted () {
	},
}
</script>
